.home{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.menu{
    width: 100%;
    height: auto;
    padding: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #fff;
}

.menu input{
    padding: 2px;
    font-weight: bold;
    font-size: medium;
    border: none;
    background-color: transparent;
}