*{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}

.post{
    width: 100%;
    height: auto;
    display: flex;
    padding: 6px;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.post_main{
    margin: 2px;
    padding-top: 5px;
    padding-bottom: 5px;
    width: 100%;
    height: auto;
    background-color: aliceblue;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
}

.post_owner_info{
    width: 100%;
    max-height: fit-content;
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 2px;
    background-color: aliceblue;
}

.post_owner_img{
    width: 25px;
    height: 25px;
    margin-left: 5px;
    border-radius: 50%;
}

.post_owner_name{
    padding-left: 7px;
    background-color: aliceblue;
}

.post_img{
    width: 100%;
    height: auto;
    padding: 5px;
    background-color: aliceblue;
    border-radius: 10px;
}

.post_activity{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: aliceblue;
}

.post_activity text{
    width: 100%;
    max-width: fit-content;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: aliceblue;
    border: 1px solid black;
    padding: 0px 8px 0px 8px;
    border-radius: 5px;
}

@media (max-width:220px) {
    .post_activity{
        flex-direction: column;
        font-size: small;
    }
}

@media (min-width:768px) {
    .post_main{
        width: 100%;
        max-width: 600px;
        max-height: 500px;
    }
    .post_img{
        max-width: 580px;
        max-height: 400px;
    }
}