.classroom{
    width: 100%;
    height: auto;
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.header{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    background-color:skyblue;
}

.title{
    background-color: transparent;
}

.classAction{
    width: 100%;
    height: auto;
    margin: 10px 0px 10px 0px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.classAction input{
    padding: 5px;
    background-color: aliceblue;
    border-radius: 10px;
    font-weight: bold;
}

.classAction input:hover{
    cursor: pointer;
    background-color: yellowgreen;
}

.classes{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.class{
    padding: 10px;
    margin: 5px 0px 5px 0px;
    width: 100%;
    height: auto;
    background-color: antiquewhite;
}

.class:hover{
    cursor: pointer;
    background-color: rgb(247, 216, 247);
}

/*  */

@media (max-width:190px) {
    .classAction{
        flex-direction: column;
        font-size: small;
    }

    .header{
        font-size: small;
    }
}

@media (max-width:400px) {
    .header{
        font-size: small;
    }
}

@media (min-width:768px) {
    .classAction{
        max-width: 768px;
    }

    .classes{
        max-width: 768px;
        
    }
}

